import React, {
  FC,
  memo,
  useReducer,
  useState,
  useRef,
  useEffect,
} from "react";
import {
  createSmartappDebugger,
  createAssistant,
  AssistantAppState,
} from "@sberdevices/assistant-client";
import "./App.css";

import { reducer } from "./store";

const initializeAssistant = (getState: any) => {
  if (process.env.NODE_ENV === "development") {
    return createSmartappDebugger({
      token: process.env.REACT_APP_TOKEN ?? "eyJhbGciOiJSUzI1NiIsInR5cCI6IkpXVCJ9..fdVNQ2lpcPypBuzMk7y0YYxXJXlryXTG-CjOVV4fRNIJfDwX9YnpS8SWlYlC5toH8fF3f3MKXemAsw2CTcmHTBhW8vKKHBLHJmc_a_wgKxEHtaFS5Qe1_GPvrwKswDnMoblrviEiTpB55WW-cixVwqwoSWUjwgA7f2u0r_6F1kmo0jXhCM9dwudssMk1PKDC9EwNujQoQD1ZC77S9h4T9DsOz6R5wtXZfPeLkL6W6skdXAoGej72Y6IgvvCcj5Tr_Q8Jvu6YlYeBzbzdE4F0nbuwb3VNxIxmXp5s72O6e4UnXB9xzebtm_UsYncTvep815zLAoz_2Wn4L--nFghVWHvrZA0AVgJbu1Rc6JDwca9LWpvds60aXm0dxcnFcVFjA9KJRRa-FLjNLwPb9iigYtxTwpHH9b70IB2KAyo-2mhjdITZy2_nJfoiOz77-22s00LlBy-pWoOUaQfjMq9tMOlKNR6MeasEJS8QZ8hUHfMWo5UARgazRQZfRxvx_9EzkPadmNxU93JLFcopI-WoUHJGzQ1qS06g3DjHSVtVAj3r153tb8Jag5LybBb5xp_48uUQpPv24hMorU9VUIONUjSTgjX1i1sJFfhrRqnCUI9t3hItqLwswn8Qs2bkOsBWKLH0-wSud7_4yFrotHOZ4Tdqrh1yJmL1pSqJkI_SyE8",
      initPhrase: `Запусти ${process.env.REACT_APP_SMARTAPP}`,
      getState,
    });
  }

  return createAssistant({ getState });
};

export const App: FC = memo(() => {
  const [appState, dispatch] = useReducer(reducer, { notes: [] });

  const [note, setNote] = useState("");

  const assistantStateRef = useRef<AssistantAppState>();
  const assistantRef = useRef<ReturnType<typeof createAssistant>>();

  useEffect(() => {
    assistantRef.current = initializeAssistant(() => assistantStateRef.current);

    assistantRef.current.on("data", ({ action }: any) => {
      if (action) {
        dispatch(action);
      }
    });
  }, []);

  useEffect(() => {
    assistantStateRef.current = {
      item_selector: {
        items: appState.notes.map(({ id, title }, index) => ({
          number: index + 1,
          id,
          title,
        })),
      },
    };
  }, [appState]);

  return (
    <main className="container">
      <form
        onSubmit={(event) => {
          event.preventDefault();
          dispatch({ type: "add_note", note });
          setNote("");
        }}
      >
        <input
          className="add-note"
          type="text"
          placeholder="Add Note"
          value={note}
          onChange={({ target: { value } }) => setNote(value)}
          required
          autoFocus
        />
      </form>
      <ul className="notes">
        {appState.notes.map((note, index) => (
          <li className="note" key={note.id}>
            <span>
              <span style={{ fontWeight: "bold" }}>{index + 1}. </span>
              <span
                style={{
                  textDecorationLine: note.completed ? "line-through" : "none",
                }}
              >
                {note.title}
              </span>
            </span>
            <input
              className="done-note"
              type="checkbox"
              checked={note.completed}
              onChange={() => dispatch({ type: "done_note", id: note.id })}
            />
          </li>
        ))}
      </ul>
    </main>
  );
});
